* {
  box-sizing: border-box;
}

body {
  color: #212121;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.container {
  text-align: center;
  max-width: 600px;
  background-color: #fff;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.breed-select {
  height: 25px;
  background-color: #ebe5dc;
  border-radius: 3%;
  justify-content: center;
  align-items: center;
}

.loader {
  width: 48px;
  height: 48px;
  background: radial-gradient(#ff3d00 0%, #ff3d00 14%, #000 15%, #000 100%) center / cover;
  border-radius: 50%;
  display: inline-block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  height: 16px;
  width: 4px;
  transform-origin: 25% 0;
  box-sizing: border-box;
  background: #ff3d00;
  animation: rotation 10s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)rotate(0deg);
}

.loader:before {
  height: 22px;
  width: 2px;
  animation-duration: 1s;
  transform: translateX(-50%)rotate(0deg);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  color: #e20a0a;
}

.cat-info {
  box-sizing: border-box;
}

img {
  max-width: 350px;
  margin-top: 30px;
}

